<template>
  <div class="component" ref="componentRef">
    <div class="box">
      <v-dialog v-model="contentDialogDelete" max-width="500px">
        <v-card>
          <v-card-title class="text-h5">Delete</v-card-title>
          <p class="dialogContent">해당 메뉴를 삭제 하시겠습니까?</p>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" text @click="closeDelete"
              >Cancel</v-btn
            >
            <v-btn color="blue darken-1" text @click="deleteItemConfirm"
              >OK</v-btn
            >
            <v-spacer></v-spacer>
          </v-card-actions>
        </v-card>
      </v-dialog>

      <div class="col">
        <div class="box rowBox">
          <div class="hyTitle">Menu Control</div>
          <v-btn color="primary" dark @click="addItem"> New Item </v-btn>
          <v-spacer></v-spacer>
          <v-btn color="primary" dark @click="saveItems"> Save </v-btn>
          <v-dialog v-model="contentDialog" max-width="500px">
            <v-card>
              <v-card-title>
                <span class="text-h6">New Item</span>
              </v-card-title>
              <v-form ref="formRef" @submit.prevent>
                <div class="regContainer">
                  <div
                    class="regBox"
                    v-for="(item, idx) in fieldItems"
                    :key="idx"
                  >
                    <!--  -->
                    <v-text-field
                      v-if="item.type === 'text'"
                      v-model="newItem[item.value]"
                      :rules="[roleRequired]"
                      :label="item.text"
                      :placeholder="item.text"
                      required
                    ></v-text-field>
                    <!--  -->
                    <v-autocomplete
                      v-if="item.type === 'shipSelect'"
                      v-model="newItem[item.value]"
                      :rules="[roleRequired]"
                      :items="shipSelectItems"
                      :label="item.text"
                      placeholder="Select..."
                      required
                      item-text="name"
                      item-value="code"
                    ></v-autocomplete>
                    <!--  -->
                    <v-autocomplete
                      v-if="item.type === 'usageSelect'"
                      v-model="newItem[item.value]"
                      :rules="[roleRequired]"
                      :items="usageSelectItems"
                      :label="item.text"
                      placeholder="Select..."
                      required
                      item-text="name"
                      item-value="code"
                    ></v-autocomplete>
                  </div>
                </div>
              </v-form>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="closeNewItem">
                  Cancel
                </v-btn>
                <v-btn color="blue darken-1" text @click="saveNewItem">
                  Save
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </div>
        <div class="box">
          <v-data-table
            fixed-header
            :headers="contentHeaders"
            :items="contentTableList"
            :items-per-page="-1"
          >
            <template v-slot:[`item.idx`]="{ index }">
              {{ index + 1 }}
            </template>
            <template v-slot:[`item.name`]="{ item }">
              <v-text-field
                v-model="item.name"
                dense
                outlined
                placeholder="menu"
                hide-details="auto"
                class="caption"
                style="width: 150px; margin: 0 auto"
              ></v-text-field>
            </template>
            <template v-slot:[`item.usage_yn`]="{ item }">
              <v-select
                v-model="item.usage_yn"
                :items="usageSelectItems"
                placeholder="usage"
                dense
                outlined
                class="caption"
                hide-details="auto"
                item-text="name"
                item-value="code"
                style="width: 150px; margin: 0 auto"
              ></v-select>
            </template>
            <template v-slot:[`item.ship_type_code`]="{ item }">
              <v-select
                v-model="item.ship_type_code"
                :items="shipSelectItems"
                placeholder="shiptype"
                dense
                outlined
                class="caption"
                hide-details="auto"
                item-text="name"
                item-value="code"
                style="width: 250px; margin: 0 auto"
              ></v-select>
            </template>
            <template v-slot:[`item.url`]="{ item }">
              <v-text-field
                v-model="item.url"
                dense
                outlined
                placeholder="menu"
                hide-details="auto"
                class="caption"
                style="width: 150px; margin: 0 auto"
              ></v-text-field>
            </template>
            <template v-slot:[`item.actions`]="{ item, index }">
              <div class="actionsBox">
                <v-icon
                  small
                  class="mr-1"
                  @click="up(contentTableList, item)"
                  v-if="index !== 0"
                >
                  mdi-arrow-up-thin
                </v-icon>
                <v-icon
                  small
                  class="mr-2"
                  @click="down(contentTableList, item)"
                  v-if="contentTableList.length - 1 !== index"
                >
                  mdi-arrow-down-thin
                </v-icon>
                <v-icon small @click="deleteItem(contentTableList, item)">
                  mdi-delete
                </v-icon>
              </div>
            </template>
          </v-data-table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, nextTick, reactive } from "vue";
import { getRequest, postRequest } from "@/services/request.js";
import { roleRequired } from "@/plugins/validation.js";

export default {
  name: "RegComponent",
  components: {},
  props: {
    obj: Object,
  },

  setup(props) {
    const { contentHeaders, contentApi } = props.obj;

    let targetList = null;
    let targetItem = null;

    //
    const formRef = ref();
    const componentRef = ref();

    //
    const contentTableList = ref([]);
    const tabTableList = ref([]);
    const shipSelectItems = ref([]);
    const usageSelectItems = ref([
      {
        name: "사용",
        code: "y",
      },
      {
        name: "미 사용",
        code: "n",
      },
    ]);

    // init
    getRequest(["menu", "shipTypes"], "", (items) => {
      shipSelectItems.value = items;
      getRequest(contentApi, "", (list) => {
        contentTableList.value = list;
      });
    });

    const defaultRegItem = {};
    const fieldItems = contentHeaders
      .filter((item) => {
        if (Object.prototype.hasOwnProperty.call(item, "type")) {
          return true;
        } else {
          return false;
        }
      })
      .map((item) => {
        defaultRegItem[item.value] = "";
        return item;
      });

    const newItem = reactive(Object.assign({}, defaultRegItem));

    //
    const contentDialog = ref(false);
    const contentDialogDelete = ref(false);

    const up = (list, item) => {
      const idx = list.indexOf(item);
      list.splice(idx, 1);
      list.splice(idx - 1, 0, item);
    };
    const down = (list, item) => {
      const idx = list.indexOf(item);
      list.splice(idx, 1);
      list.splice(idx + 1, 0, item);
    };

    const addItem = () => {
      contentDialog.value = true;
    };
    const deleteItem = (list, item) => {
      targetList = list;
      targetItem = item;
      contentDialogDelete.value = true;
    };
    const deleteItemConfirm = () => {
      const idx = targetList.indexOf(targetItem);
      targetList.splice(idx, 1);
      closeDelete();
      // nextTick(() => {
      // });
    };
    const closeDelete = () => {
      contentDialogDelete.value = false;
      nextTick(() => {
        targetList = null;
        targetItem = null;
      });
    };
    const saveNewItem = () => {
      contentTableList.value.push(Object.assign({}, newItem));
      closeNewItem();
    };
    const closeNewItem = () => {
      contentDialog.value = false;
      nextTick(() => {
        formRef.value.reset();
        Object.assign(newItem, defaultRegItem);
      });
    };

    const saveItems = () => {
      postRequest(
        ["menu", "saveMenus"],
        Array.from(contentTableList.value),
        (result) => {
          alert("데이터 저장이 완료 되었습니다.");
        }
      );
    };

    return {
      roleRequired,

      componentRef,
      formRef,
      //
      newItem,
      //
      fieldItems,

      up,
      down,

      addItem,
      saveItems,

      usageSelectItems,
      shipSelectItems,
      contentDialog,
      contentDialogDelete,
      contentHeaders,
      contentTableList,
      tabTableList,
      deleteItem,
      deleteItemConfirm,
      closeNewItem,
      closeDelete,
      saveNewItem,
    };
  },
};
</script>

<style></style>
