<template>
  <div class="containerFrame">
    <div class="tabBox">
      <v-tabs v-model="tabIdx">
        <v-tab v-for="(item, index) in tabs" :key="index">
          {{ item.name }}
        </v-tab>
      </v-tabs>
    </div>
    <div class="fix">
      <template v-for="(item, index) in tabs">
        <div class="component" :key="index" v-if="tabIdx === index">
          <div class="box">
            <div class="col">
              <iframe class="customFrame" :src="item.url"></iframe>
            </div>
          </div>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
import { ref } from "vue";
import { getRequest } from "@/services/request.js";
// import { useAuthStore } from "@/stories/mainStore.js";
export default {
  name: "HomeView",
  setup() {
    // console.log(useAuthStore());

    //
    const tabIdx = ref(0);
    const tabs = ref([]);
    getRequest(["menu", "menus"], "", (items) => {
      tabs.value = items.filter((item) => {
        return item.usage_yn === "y";
      });
    });
    return {
      tabs,
      tabIdx,
    };
  },
};
</script>

